import React from "react"
import { graphql } from "react-apollo"
import gql from "graphql-tag"
import Select from "../elements/Select"

class AccessSelect extends React.Component {
    render() {
        let options = {};
        if (this.props.data.site) {
            this.props.data.site.accessIds.forEach(accessId => {
                options[accessId.id] = accessId.description
            })
        }

        let defaultValue = 0;
        if (this.props.data.site) {
            defaultValue = this.props.data.site.defaultAccessId
        }

        return (
            <Select className={this.props.className} options={options} onChange={this.props.onChange} value={defaultValue} />
        )
    }
}

const QUERY = gql`
    query AccessSelect {
        site {
            defaultAccessId
            accessIds {
                id
                description
            }
        }
    }
`

export default graphql(QUERY)(AccessSelect)



